import React, { useState, useEffect } from "react";
import {
  AppstoreOutlined,
  MailOutlined,
  SettingOutlined,
  LeftOutlined,
  RightOutlined,
} from "@ant-design/icons";

import { Input, Tabs, Menu, Row, Col } from "antd";
import { useTranslation } from "react-i18next";
import { NavLink, useLocation } from "react-router-dom";
import { gethelplist, gethelpindex, gethelpdetial } from "../../../api/support";
import moment from "moment";
// import "./help.css";
import "./help.less";
export default function Help() {
  const { t } = useTranslation();
  const [current, setCurrent] = useState("1");
  const { TabPane } = Tabs;
  const [contents, setcontents] = useState("");
  const [keywords, setkeywords] = useState("");
  const [problems, setproblems] = useState([]);
  const [activekey, setactivekey] = useState("1");
  const [openKeys, setopenKeys] = useState([]);

  const [detial, setdetial] = useState("");

  const [items, setitems] = useState([]);
  //decodeURIComponent 解密

  const { SubMenu } = Menu;
  const checkMenu = (e) => {
    let str_after = Number(e.key.split("-")[1]);
    setCurrent(str_after);
    setopenKeys(e.keyPath);
  };
  const gethelpdetials = (key) => {
    gethelpdetial({ id: key }).then((res) => {
      console.log(res.data.data.help, "gethelpdetial");
      setdetial(res.data.data.help);
      setcontents(res.data.data.help.content);
    });
  };
  const gethelplists = () => {
    gethelpindex().then((res) => {
      console.log(res.data.data.index, "gethelpindex");
      setproblems(res.data.data.index);
    });
  };

  const todetial = (menuid, key) => {
    console.log(key, menuid, "menuidkey");
    setactivekey("2");
    setCurrent(key);
    setopenKeys([menuid + "-" + key, JSON.stringify(menuid)]);
    console.log(openKeys, current, "todetial");
  };
  const searchlist = () => {
    setactivekey("2");
    getmenulist();
  };
  const getmenulist = () => {
    gethelplist({ keywords }).then((res) => {
      console.log(res.data.data.list, "getmenulist");
      setitems(res.data.data.list);
      res.data.data.list.map((item) => {
        if (item.helps.length > 0) {
          item.helps.map((it) => {
            if (it.search) {
              setCurrent(it.id);
              setopenKeys([item.id + "-" + it.id, JSON.stringify(item.id)]);
              gethelpdetials(it.id);
              console.log(current, "setCurrent");
            }
          });
        }
      });
    });
  };
  useEffect(() => {
    gethelplists();
    getmenulist();
  }, []);
  useEffect(() => {
    gethelpdetials(current);
  }, [current]);

  const changetab = (e) => {
    setactivekey(e);
    if (e === "2") {
      searchlist();
    }

    console.log(e, detial, "eeees");
  };
  const tosearch = (e) => {
    console.log(e, 2222);
    setkeywords(e.target.value);
  };
  const downloadfile = (url) => {
    console.log(url, "url");
    const downloadElement = document.createElement("a");

    downloadElement.href = url;
    downloadElement.download = url.split("^")[1]; // 下载后文件名

    document.body.appendChild(downloadElement);

    downloadElement.click(); // 点击下载
  };
  const onClicks = (e) => {
    console.log(e, "oooo");
  };
  const onOpenChange = (e) => {
    setopenKeys(e);
    console.log(e, "onOpenChange");
  };
  return (
    <div className="help flex  just_center">
      <div className="help_top">
        <div className="bg_right"></div>
        <div className="help_title">我们能帮你什么吗？</div>
        <div className="help_searh flex align_center">
          <img
            onClick={searchlist}
            src={require("@/assets/images/search.png")}
          />
          <Input
            placeholder="请输入"
            bordered={false}
            onPressEnter={(e) => searchlist(e)}
            onChange={tosearch}
          />
        </div>
        <div className="help_tabs">
          <Tabs activeKey={activekey} onTabClick={changetab}>
            <TabPane tab="常见问题" key="1">
              <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
                <div className="usual flex">
                  {problems.length > 0
                    ? problems.map((item, index) => {
                        return item.id ? (
                          <Col xs={{ span: 24 }} lg={{ span: 8 }}>
                            <div key={item.id} className="pro_item flex">
                              <img
                                src={require(`@/assets/images/pro${
                                  index + 1
                                }.png`)}
                              ></img>
                              <div className="pro_itemright">
                                <div className="pro_itemname">{item.name}</div>
                                {item.helps
                                  ? item.helps.map((it, ind) => {
                                      return (
                                        <div
                                          className="pro_itemcontent"
                                          key={ind}
                                          onClick={() =>
                                            todetial(item.id, it.id)
                                          }
                                        >
                                          {it.title}
                                        </div>
                                      );
                                    })
                                  : ""}
                              </div>
                            </div>
                          </Col>
                        ) : (
                          ""
                        );
                      })
                    : ""}
                </div>
              </Row>
            </TabPane>
            <TabPane tab="问题汇总" key="2">
              <div className="total">
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
                  <Col xs={{ span: 24 }} lg={{ span: 6 }}>
                    <div className="left_menu">
                      <div className="menu_top">文档目录</div>
                      <div className="menu_title">
                        <Menu
                          mode="inline"
                          selectedKeys={openKeys}
                          // items={items}
                          onClick={checkMenu}
                          openKeys={openKeys}
                          onOpenChange={onOpenChange}
                        >
                          {items.map((item) => {
                            if (item.helps.length > 0) {
                              return (
                                <SubMenu key={item.id} title={item.name}>
                                  {item.helps.map((node) => {
                                    return (
                                      <Menu.Item key={item.id + "-" + node.id}>
                                        {node.title}
                                      </Menu.Item>
                                    );
                                  })}
                                </SubMenu>
                              );
                            }
                            return (
                              <Menu.Item key={item.id}>{item.name}</Menu.Item>
                            );
                          })}
                        </Menu>
                      </div>
                    </div>
                  </Col>
                  <Col xs={{ span: 24 }} lg={{ span: 18 }}>
                    <div className="right_content">
                      <p className="detialtitle">{detial.title}</p>
                      {detial ? (
                        <div className="subtimebar flex">
                          <div className="keywords" title={detial.keywords}>
                            关键字：{detial.keywords}
                          </div>

                          <div
                            className="keywords"
                            title={moment(detial.create_time * 1000).format(
                              "YYYY-MM-DD "
                            )}
                          >
                            时间：
                            {detial.create_time
                              ? moment(detial.create_time * 1000).format(
                                  "YYYY-MM-DD "
                                )
                              : ""}
                          </div>
                        </div>
                      ) : (
                        ""
                      )}
                      <div
                        className="contents"
                        dangerouslySetInnerHTML={{ __html: contents }}
                      />
                      {detial ? (
                        <div className="attachment_box">
                          <span> 附件：</span>
                          {detial.attachment && detial.attachment.length > 0
                            ? detial.attachment.map((item, index) => {
                                return (
                                  <span
                                    className="download"
                                    onClick={() => {
                                      downloadfile(item);
                                    }}
                                  >
                                    {item.split("^")[1]} &emsp;
                                  </span>
                                );
                              })
                            : ""}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </Col>
                </Row>
              </div>
            </TabPane>
          </Tabs>
        </div>
      </div>
    </div>
  );
}
